@import url("https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap");
body {
  font-family: "Roboto", sans-serif;
  background: #c9c9c9; }

* {
  box-sizing: border-box; }

.main {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  min-width: 300px;
  max-width: 320px;
  width: 100%;
  margin-top: 15%;
  margin-left: auto;
  margin-right: auto;
  background: #444545; }

.wrapper {
  padding: 10px 0;
  margin: 0 15px; }

.title {
  color: #c5e0d8;
  text-align: center;
  margin: 0;
  font-size: 28px;
  padding: 10px 0; }

.row-item {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 3em; }
  .row-item > .title {
    font-weight: bold;
    font-size: 16px;
    padding: 0; }

#length {
  padding-left: 3px;
  color: #b5ffe9;
  background: #707078; }

#password {
  color: #fae8eb;
  display: flex;
  align-items: center;
  position: relative;
  font-size: 20px;
  padding: 5px;
  height: 100%;
  background: #707078;
  width: 100%; }

.btn-copy {
  position: absolute;
  right: 5px;
  width: 35px;
  height: 35px;
  font-size: 20px;
  padding: 0; }

#generate {
  font-weight: bold;
  font-size: 20px;
  width: 100%;
  height: 2em;
  margin-bottom: 20px; }

input {
  outline: none;
  border: none;
  margin: 0; }

button {
  outline: none;
  border: none;
  background: #b5ffe9;
  color: #444545; }
  button:hover {
    cursor: pointer; }
